.App {
  text-align: center;
}

.titulo {
  font-size: 60px;
  color: #0091cf;
  text-align: left;
  padding-bottom: 35px;
}
.containers {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-flow: row wrap;
}
.GetDataContainer {
  background-color: #fafafa;
  border-radius: 15px;
  padding: 15px;
  width: 60%;
  margin-bottom: 15px;
}
.ResultsContainer {
  border-radius: 10px;
  padding: 25px;
  width: 30%;
  margin-bottom: 15px;
}

.Label {
  font-size: 30px;
  line-height: 30px;
}

.UserInput {
  text-align: left;
  padding-top: 20px;
  padding-bottom: 20px;
}
.InputBox {
  padding-top: 10px;
}
.InputBox input {
  text-align: left;
  font-size: 38px;
  padding: 10px;
  border-radius: 8px;
  border: #c0c0c0 1px solid;
}

.InputHolder {
  display: flex;
}
.InputHolder input {
  width: 100%;
}

.TextDesc {
  text-align: left;
  font-size: 18px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.Footer {
  text-align: center;
  font-size: 14px;
  color: #535353;
  padding-top: 30px;
}
.IMCValue {
  font-size: 120px;
  font-weight: bold;
}
.IMCName {
  font-size: 40px;
  color: #2e790b;
  padding-bottom: 20px;
}
.IMCImage img {
  max-width: 40%;
}
.imc_res_none {
  background-color: #c5c5c5;
}
.imc_res_abaixo_peso {
  background-color: #eef4f4;
}
.imc_res_normal {
  background-color: #e1f7d0;
}
.imc_res_acima_peso {
  background-color: #fff3bf;
}
.imc_res_obeso {
  background-color: #fad8d9;
}
.imc_abaixo_peso {
  color: #aed9d0;
}
.imc_normal {
  color: #9ee656;
}
.imc_acima_peso {
  color: #ffd900;
}
.imc_obeso {
  color: #f17983;
}
.imc_none {
  color: #878787;
}
.imc_none_question {
  font-size: 220px;
  line-height: 200px;
}

@media only screen and (max-width: 960px) {
  .GetDataContainer,
  .ResultsContainer {
    width: 100%;
  }
  .titulo {
    font-size: 50px;
  }
}
@media only screen and (max-width: 600px) {
  .GetDataContainer,
  .ResultsContainer {
    width: 100%;
  }
  .titulo {
    font-size: 40px;
  }
}
@media only screen and (max-width: 425px) {
  .GetDataContainer,
  .ResultsContainer {
    width: 100%;
  }
  .titulo {
    font-size: 25px;
  }
}
